"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Header } from "@/components/header"

export default function DebugPage() {
  const [debugData, setDebugData] = useState<any>(null)
  const [loading, setLoading] = useState(false)
  const [resetLoading, setResetLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const fetchDebugData = async () => {
    try {
      setLoading(true)
      setError(null)

      const response = await fetch("/api/debug-fila")
      const data = await response.json()

      setDebugData(data)
    } catch (err) {
      console.error("Erro ao buscar dados de depuração:", err)
      setError("Não foi possível buscar os dados de depuração.")
    } finally {
      setLoading(false)
    }
  }

  const resetFilaData = async () => {
    try {
      setResetLoading(true)
      setError(null)

      const response = await fetch("/api/reset-fila", {
        method: "POST",
      })
      const data = await response.json()

      setDebugData(data)
      alert("Dados da fila resetados com sucesso!")
    } catch (err) {
      console.error("Erro ao resetar dados da fila:", err)
      setError("Não foi possível resetar os dados da fila.")
    } finally {
      setResetLoading(false)
    }
  }

  useEffect(() => {
    fetchDebugData()
  }, [])

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="flex-1 p-6">
        <div className="max-w-4xl mx-auto space-y-6">
          <div className="flex justify-between items-center">
            <h1 className="text-2xl font-bold">Página de Depuração</h1>
            <div className="space-x-2">
              <Button onClick={fetchDebugData} disabled={loading} variant="outline">
                {loading ? "Carregando..." : "Atualizar Dados"}
              </Button>
              <Button onClick={resetFilaData} disabled={resetLoading} variant="destructive">
                {resetLoading ? "Resetando..." : "Resetar Fila"}
              </Button>
            </div>
          </div>

          {error && <div className="bg-red-50 p-4 rounded-md text-red-600">{error}</div>}

          <Card>
            <CardHeader>
              <CardTitle>Dados da Fila</CardTitle>
            </CardHeader>
            <CardContent>
              {debugData ? (
                <div className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div className="bg-blue-50 p-4 rounded-md">
                      <h3 className="font-medium mb-2">Clientes na Fila</h3>
                      <p className="text-2xl font-bold">{debugData.clientesNaFilaLength}</p>
                    </div>
                    <div className="bg-green-50 p-4 rounded-md">
                      <h3 className="font-medium mb-2">Clientes em Atendimento</h3>
                      <p className="text-2xl font-bold">{debugData.clientesEmAtendimentoLength}</p>
                    </div>
                  </div>

                  <div>
                    <h3 className="font-medium mb-2">Dados Completos:</h3>
                    <pre className="bg-gray-100 p-4 rounded-md overflow-auto max-h-96 text-xs">
                      {JSON.stringify(debugData, null, 2)}
                    </pre>
                  </div>
                </div>
              ) : loading ? (
                <p>Carregando dados...</p>
              ) : (
                <p>Nenhum dado disponível</p>
              )}
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Endpoints Disponíveis</CardTitle>
            </CardHeader>
            <CardContent>
              <ul className="list-disc pl-5 space-y-2">
                <li>
                  <code className="bg-gray-100 px-2 py-1 rounded">/api/fila/clientes</code> - Lista de clientes na fila
                </li>
                <li>
                  <code className="bg-gray-100 px-2 py-1 rounded">/api/fila/atendimentos</code> - Lista de clientes em
                  atendimento
                </li>
                <li>
                  <code className="bg-gray-100 px-2 py-1 rounded">/api/debug-fila</code> - Dados de depuração da fila
                </li>
                <li>
                  <code className="bg-gray-100 px-2 py-1 rounded">/api/reset-fila</code> - Resetar dados da fila (POST)
                </li>
              </ul>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  )
}

